.o-tab-active{
    background-color: #298ecc !important;
}

.arrow-right {
    width: 0; 
    height: 0; 
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    
    border-left: 10px solid transparent;
   
    margin-left: 0.4rem;
  }

  .o-arrow-active {
    border-left: 10px solid #cc293c !important;
  }

  .o-input{
    color:white;
    padding:"0.2rem";
    border: none;
    border-radius: 6px;
    background-color:gray;
  }

 .o-button-style{
    width: 100%;
    height: 100%;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    display: flex;

}

.custom-input-file {
  
  cursor: pointer;

 
  overflow: hidden;
 
  position: relative;

  
}

.custom-input-file .input-file {
 border: 10000px solid transparent;
 cursor: pointer;
 font-size: 10000px;
 margin: 0;
 opacity: 0;
 outline: 0 none;
 padding: 0;
 position: absolute;
 right: -1000px;
 top: -1000px;
}